.mobile-menu {
  align-items: center;
  padding: 20px;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999999;
  display: none;
  border-right: 1px solid rgba(255, 255, 255, 0.4);
}

@media only screen and (max-width: 1025px) {
  .mobile-menu {
    display: flex;
  }
}

.mobile-menu__btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  height: 30px;
  cursor: pointer;
  transition: .4s;
}

.mobile-menu__icon {
  display: block;
  background: white;
  height: 2px;
  transition: .4s;
  position: relative;
  width: 90%;
}

.mobile-menu__icon::after {
  display: block;
  background: white;
  height: 2px;
  transition: .4s;
  content: "";
  position: absolute;
  width: 100%;
  top: 8px;
}

.mobile-menu__icon::before {
  display: block;
  background: white;
  height: 2px;
  transition: .4s;
  content: "";
  position: absolute;
  width: 100%;
  top: -8px;
}

.mobile-menu__container {
  position: absolute;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 71px;
  left: 0;
  right: 0;
  z-index: 999;
  height: 0;
  opacity: 1;
  transition: .5s;
  overflow: hidden;
  background-color: #902156;
}

.mobile-menu__list {
  transition: .5s;
  list-style: none;
  padding-left: 0;
  margin-top: -50px;
}

.mobile-menu__item {
  font-size: 26px;
  padding-bottom: 15px;
}

.mobile-menu__link {
  text-decoration: none;
  color: #fff;
}

.mobile-menu__checkbox {
  display: none;
}

.mobile-menu__checkbox:checked ~ .mobile-menu__nav {
  opacity: 1;
}

.mobile-menu__checkbox:checked ~ .mobile-menu__container {
  height: 100%;
}

.mobile-menu__checkbox:checked ~ .mobile-menu__btn .mobile-menu__icon {
  background: transparent;
  padding-bottom: 18px;
}

.mobile-menu__checkbox:checked ~ .mobile-menu__btn .mobile-menu__icon::before {
  top: 8px;
}

.mobile-menu__checkbox:checked ~ .mobile-menu__checkbox:checked ~ .mobile-menu__btn .mobile-menu__icon::after {
  top: 2px;
}

.mobile-menu__checkbox:checked ~ .mobile-menu__btn .mobile-menu__icon::after {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.mobile-menu__checkbox:checked ~ .mobile-menu__btn .mobile-menu__icon::before {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

